.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 70px;
  padding: 0 40px;
  box-shadow: 0px 4px 36px -21px rgba(0, 0, 0, 0.75);
}

.link {
  font-size: 18px;
  color: #aaaaaa;
  text-decoration: none;
}

.link:hover {
  color: #888888;
}

.link:not(:last-child) {
  margin-right: 30px;
}

.active {
  font-size: 18px;
  text-decoration: none;
  color: #d45470;
}

.active:not(:last-child) {
  margin-right: 30px;
}
